* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

#nav {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 5rem;   
    background-color: #ffffff;
    box-shadow: 0px 2.3px 2px rgba(0, 0, 0, 0.20);
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    padding: 0 9rem;
    z-index: 100;
}

#nav-logo {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    gap: 1rem;
    width: 13rem;
    height: 50%;
    padding: 0.5rem;
    font-family: "Helvetica Neue",Helvetica,Arial;
    font-size: 1rem;
    font-weight: 500;
    color: transparent;
    background-size: 50%;
    background-clip: text;
    background: conic-gradient(#824220 0%, #824220 33%, #288220 33%, #288220 66%, #207882 66%, #207882 99%);
    -webkit-background-clip: text;
    background-clip: text;
    transform: rotate(2deg);
    animation: expand-rev 0.5s ease forwards;
    border: rgb(195, 195, 195) 2px solid;
}


#nav-logo:hover {

    -webkit-transition-property: color, background-color;
    transition-property: color, background-color, border;
    -webkit-transition-duration: 0.4s;
    transition-duration: 0.4s;
    -moz-osx-font-smoothing: grayscale;
    cursor: pointer;
    animation: shake 0.5s ease forwards, expand 0.5s ease forwards;
}


.nav-text {
    text-decoration: underline;
    color: black;
}

.nav-text:hover {
    cursor: pointer;
    color: #4f4f4f;
}
.module-container
{
    margin-top: 7rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 1440px;
    height: 100%;
    padding-top: 20px;
    gap: 6rem;
}

.module {
    margin-top: 10px;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    gap: 6rem;
    width: 78%;
    height: 100%;
    
    
}

.module-title {
    font-size: 42px;
    font-family: 'Roboto', Courier, monospace;
    font-weight: 700;@keyframes shake {
    0% {
        transform: translate(1px, 1px) rotate(0deg);
    } 
    20% {
        transform: translate(-3px, 0px) rotate(1deg);
    }
    40% {
        transform: translate(1px, -1px) rotate(1deg);
    }
    60% {
        transform: translate(-3px, 1px) rotate(0deg);
    }
    80% {
        transform: translate(-1px, -1px) rotate(1deg);
    }
    100% {
        transform: translate(1px, -2px) rotate(-1deg);
    }
    }

    @keyframes expand {
    0% {
        background-size: 50%;
        background-position: 0 0;
    }
    20% {
        background-size: 55%;
        background-position: 0 1em;
    }
    100% {
        background-size: 325%;
        background-position: -13em -4em;
    }
    }
    line-height: 50.4px;
}
.module-content {
    display: flex;
    flex-direction: column;
    align-items: start;
    justify-content: space-between;
    width: 23rem;
    height: 100%;
    gap: 1rem;
    font-family: 'RobotoLight';
    line-height: 150%;
    font-weight: 300;

}
.module-image {
    width: 31.5rem;
    height: 21rem;
}

.module-buttons {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: start;
    width: 100%;
    height: 100%;
    gap: 1rem;
}
.module-button {
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #207882;
    border-radius: 5rem;
    border: none; 
    color: white;
    font-weight: 700;
    font-size: 1rem;
    width: 8rem;
    height: 3rem;
    font-family: 'Roboto';
    line-height: 16px;

    &:hover {
        cursor: pointer;
        background-color: #2c99ac;
    }

}

.module-reset-button {
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #824220;
    border-radius: 5rem;
    border: none; 
    color: white;
    font-weight: 700;
    font-size: 1rem;
    width: 8rem;
    height: 3rem;
    font-family: 'Roboto';
    line-height: 16px;

    &:hover {
        cursor: pointer;
        background-color: #974d25;
    }
    
}

@font-face {
    font-family: Roboto;
    src: url('../fonts/Roboto-Bold.ttf');
  }

  @font-face {
    font-family: RobotoLight;
    src: url('../fonts/Roboto-Light.ttf');
  }

#modal-overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    background-color: rgba(0, 0, 0, 0.5);

    align-items: center;
    justify-content: center;
    z-index: 100;
}

#modal { 
    position: relative;
    width: 57rem;
    height: 12rem;
    background-color: white;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 1rem;
    
}

#modal-content {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 80%;
    gap: 1rem;



}

#modal-header {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    height: 3rem;
    font-family: 'Roboto';
    font-size: 1.8rem;
    line-height: 120%;
    color:  #121212;
}

#modal-body {
    display: flex;
    flex-direction: row;
    align-items: center;
    gap: 3px;
    width: 100%;
    height: 3rem;
    font-family: 'RobotoLight';
    line-height: 150%;
}

#modal-footer {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    height: 3rem;
}   

#modal-nav {
    position: absolute;
    top: 2.1rem;
    right: 2.1rem;
    display: flex;
 
    height: 3rem;

    > #modal-nav-close {
        
        border: none;
        background-color: white;
        width: 1.6rem;
        height: 1.6rem;
        background: url('https://api.iconify.design/material-symbols/close-rounded.svg') no-repeat center center / contain;
        content: url('https://api.iconify.design/material-symbols/close-rounded.svg');
    }

    > #modal-nav-close:hover {
        cursor: pointer;
    }
}

.is-active {
    display: flex;
}

.is-hidden {
    display: none;
}

.reverse  {
    align-items: end;
    justify-content: end;
    text-align: end;
    
}

.smooth-color {
  -webkit-transition: color 0.3s;
  transition: color 0.3s;
  cursor: pointer;
}

